@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: "Open Sans Condensed", sans-serif;
  padding: 0 60px;
}

a {
  text-decoration: none;
  color: black;
}

* {
  box-sizing: border-box;
}

.custom-button{min-width:165px;width:auto;height:50px;letter-spacing:0.5px;line-height:50px;padding:0 35px 0 35px;font-size:15px;background-color:black;color:white;text-transform:uppercase;font-family:"Open Sans Condensed";font-weight:bolder;border:none;cursor:pointer;display:flex;justify-content:center}.custom-button:hover{background-color:white;color:black;border:1px solid black}.custom-button.google-sign-in{background-color:#4285f4;color:white}.custom-button.google-sign-in:hover{background-color:#357ae8;border:none}.custom-button.inverted{background-color:white;color:black;border:1px solid black}.custom-button.inverted:hover{background-color:black;color:white}.custom-button.cart-added:active{background-color:#3faa56;border:none}.custom-button.cart-added:focus{outline:none}

.billboard-container{width:100%;color:white;text-align:center}.billboard-container .image-container{height:50vh;background-image:linear-gradient(rgba(0,0,0,0.4), rgba(0,0,0,0.4)),url("https://images.unsplash.com/photo-1544441893-675973e31985?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1350&q=80");background-size:cover;border-radius:10px;display:flex}.billboard-container .image-container:before{background:#000;content:"";position:absolute;z-index:2}.billboard-container .image-container .search-container{width:100%;height:100%;display:flex;flex-direction:column;justify-content:center;align-items:center}.billboard-container .image-container .search-container .name{font-size:55px;margin:0}.billboard-container .image-container .search-container .suggestion{font-size:25px}.billboard-container .image-container .search-container .search-form{display:flex;flex-direction:column;justify-content:center;align-items:center}.billboard-container .image-container .search-container .search-form label{font-size:25px;margin-bottom:10px}.billboard-container .image-container .search-container .search-form .billboard-search{width:35vw;height:50px;border:none;border-radius:2px;text-indent:50px;font-family:"Ubuntu", sans-serif;font-size:20px}.billboard-container .image-container .search-container .search-form .billboard-search:focus{outline:4px solid rgba(77,76,76,0.253)}.billboard-container .image-container .search-container .search-form .search-bar{display:flex;flex-direction:row}

.menu-item{width:15vw;height:75vh;display:flex;flex-direction:column;align-items:center;justify-content:center;margin:0 7.5px 15px;overflow:hidden;border-radius:10px;border:1px solid rgba(128,128,128,0.294)}@media (max-width: 768px){.menu-item{font-size:25px;width:75vw;height:40vh}}.menu-item:hover{cursor:pointer}.menu-item:hover .background-image{transform:scale(1.1);transition:transform 2s cubic-bezier(0.25, 0.45, 0.45, 0.95);opacity:0.5;overflow:hidden}.menu-item:hover .title{visibility:visible}.menu-item .background-image{width:100%;height:100%;background-position:center;background-size:cover}.menu-item .title{font-weight:bold;margin-bottom:6px;font-size:42px;color:#181717;visibility:hidden;position:absolute}@media (max-width: 768px){.menu-item .title{font-size:25px}}

h1{text-align:center}.directory-menu{width:75vw;display:flex;justify-content:center;text-align:center;flex-direction:row}@media (max-width: 768px){.directory-menu{font-size:25px;flex-direction:column}}

.homepage{display:flex;flex-direction:column;align-items:center;padding:20px 0}

.collection-preview{display:flex;flex-direction:column;margin-bottom:30px}.collection-preview .title{font-size:28px;margin-bottom:25px}.collection-preview .preview{display:flex;justify-content:space-between}

.collection-item{width:18vw;display:flex;flex-direction:column;height:450px;align-items:center;position:relative;cursor:pointer}.collection-item .image{width:95%;height:100%;background-size:cover;background-repeat:no-repeat;background-position:center}.collection-item:hover .image{-webkit-filter:saturate(100%) brightness(60%) hue-rotate(0deg);filter:saturate(100%) brightness(60%) hue-rotate(0deg)}.collection-item:hover .item-details{opacity:1;background-color:white;color:black;border:1px solid #e7e4e4}.collection-item .item-details{display:flex;flex-direction:row;justify-content:space-between;width:95%;background-color:black;color:white;font-size:25px;text-align:center;padding:0 10px}.collection-item .item-details .price{margin-bottom:10px;font-size:25px}

.collection-page{display:flex;flex-direction:column}.collection-page .title{font-size:38px;margin:10px auto 10px}.collection-page .items{display:grid;grid-template-columns:1fr 1fr 1fr 1fr 1fr}.collection-page .items .collection-item{margin-bottom:30px}

.detail-page{display:flex;flex-direction:row;justify-content:center;margin:100px 0}.detail-page .item-image-container{width:450px;display:flex;justify-content:center;align-items:center}.detail-page .item-image-container .image{width:380px;height:550px;background-size:100% 100%}.detail-page .item-details-container{width:380px;display:flex;flex-direction:column;justify-content:space-between;border-left:1px solid #d3d3d3;padding-left:30px;margin-left:50px}.detail-page .item-details-container .item-price{font-size:35px;text-align:right;font-weight:bold}.detail-page .item-details-container .item-description{font-size:20px}

.search-results-page{display:flex;flex-direction:column}.search-results-page .title{font-size:38px;margin:30px auto 50px}.search-results-page .items{display:grid;grid-template-columns:1fr 1fr 1fr 1fr 1fr}.search-results-page .items .collection-item{margin-bottom:30px}

.cart-icon{width:45px;height:45px;position:relative;display:flex;align-items:center;justify-content:center;cursor:pointer;bottom:2px}.cart-icon .shopping-icon{width:38px;height:38px}.cart-icon .item-count{position:absolute;font-size:15px;font-weight:bold;bottom:10px;-webkit-user-select:none;-ms-user-select:none;user-select:none}

.cart-item{width:100%;display:flex;height:80px;margin-bottom:15px}.cart-item img{width:30%}.cart-item .item-details{width:70%;display:flex;flex-direction:column;align-items:flex-start;justify-content:center;padding:10px 20px}.cart-item .item-details .name{font-size:16px}

.cart-dropdown{position:absolute;width:240px;height:340px;display:flex;flex-direction:column;padding:20px;border:1px solid black;background-color:white;top:90px;right:40px;z-index:5}.cart-dropdown .cart-items{height:240px;display:flex;flex-direction:column;overflow:scroll}.cart-dropdown .empty-message{text-align:center;font-size:20px;color:red;font-style:italic;margin:50px auto}.cart-dropdown button{margin-top:auto}

.header{height:140px;width:100%;display:flex;justify-content:space-between;border-bottom:1px solid gray}.header .logo-container{padding:25px;display:flex;flex-direction:column;font-size:20px;font-weight:700;transition-duration:0.5s}.header .logo-container:hover{color:#412f55;transform:scale(1.03)}.header .logo-container:hover .logo{background-image:url(/react-ecommerce/static/media/sport-wear-colored.c1047b0c.png)}.header .logo-container .logo{width:100px;height:100px;background-image:url(/react-ecommerce/static/media/sport-wear.5f29d90c.png);background-size:contain;background-repeat:no-repeat;background-position:center;transition-duration:0.1s}.header .options{width:50%;height:140px;display:flex;align-items:center;justify-content:flex-end;font-size:25px}.header .options .option{padding:10px 45px;cursor:pointer;transition-duration:1s}.header .options .option:hover{color:#3a55cf;text-decoration:underline}

.group{position:relative;margin:45px 0}.group .form-input{background:none;background-color:white;color:grey;font-size:18px;padding:10px 10px 10px 5px;display:block;width:100%;border:none;border-radius:0;border-bottom:1px solid grey;margin:25px 0}.group .form-input:focus{outline:none}.group .form-input:focus ~ .form-input-label{top:-14px;font-size:12px;color:#000}.group input[type="password"]{letter-spacing:0.3em}.group .form-input-label{color:grey;font-size:16px;font-weight:normal;position:absolute;pointer-events:none;left:5px;top:10px;transition:300ms ease all}.group .form-input-label.shrink{top:-14px;font-size:12px;color:#000}

.sign-in{width:380px;display:flex;flex-direction:column}.sign-in .title{margin:10px 0}.sign-in .buttons{display:flex;justify-content:space-between}

.sign-up{display:flex;flex-direction:column;width:380px}.sign-up .title{margin:10px 0}

.sign-in-and-sign-up{width:850px;display:flex;justify-content:space-between;margin:30px auto}

.checkout-item{width:100%;display:flex;min-height:100px;border-bottom:1px solid darkgrey;padding:15px 0;font-size:20px;align-items:center}.checkout-item .image-container{width:23%;padding-right:15px}.checkout-item .image-container img{width:100%;height:100%}.checkout-item .name,.checkout-item .quantity,.checkout-item .price{width:23%}.checkout-item .quantity{display:flex}.checkout-item .quantity .arrow{cursor:pointer}.checkout-item .quantity .value{margin:0 10px;font-weight:bold}.checkout-item .remove-button{padding-left:12px;cursor:pointer}

.checkout-page{width:55%;min-height:90vh;display:flex;flex-direction:column;align-items:center;margin:50px auto 0}.checkout-page .checkout-header{width:100%;padding:10px 0;display:flex;justify-content:space-between}.checkout-page .checkout-header .header-block{text-transform:capitalize;width:23%}.checkout-page .checkout-header .header-block:last-child{width:8%}.checkout-page .cart-empty-message{font-size:30px}.checkout-page .total{width:100%;margin:30px;display:flex;flex-direction:row;justify-content:space-between;align-items:center;font-size:30px;font-family:"Ubuntu", sans-serif}.checkout-page .total .test-warning{font-size:15px;text-align:center;font-weight:bold;background-color:#e0dddd;padding:5px}.checkout-page .total .payment{display:flex;flex-direction:column}

